@import '../../styles/mixins';

.sb-checkbox__label {
  align-items: flex-start;
  cursor: pointer;
  display: flex;
  flex-direction: row;
}

.sb-checkbox__input {
  margin-right: var(--sb-checkbox-margin-right, 8px);
  margin-top: var(--sb-checkbox-margin-top, 1px);
}

.sb-checkbox_disabled {
  .sb-checkbox__label {
    @include disabled-shake();
    cursor: not-allowed;
  }
}

.sb-checkbox__label-text {
  color: var(--sbt-theme-tertiary-color-90);
  font-size: var(--sb-checkbox-label-font-size, var(--sbt-font-size-30));
  grid-area: labelText;
  line-height: var(--sb-checkbox-label-line-height, var(--sbt-line-height-ui));
}

.sb-checkbox__helper {
  color: var(--sbt-theme-tertiary-color-50);
  display: block;
  font-size: var(--sbt-font-size-20);
  line-height: var(--sbt-line-height-75);
}

.sb-checkbox_error {
  box-shadow: 0 0 3px 1px var(--sbt-color-error-50);
}
